import { render, screen } from '@testing-library/react';
import '@testing-library/jest-dom';
import ModuleHeader from '@/components/common/ModuleHeader';

/**
 * üß™ YemekZen Core Components Unit Tests
 * 
 * Bu test suite temel component'lerin doƒüru √ßalƒ±≈ütƒ±ƒüƒ±nƒ± kontrol eder
 */

describe('ModuleHeader Component', () => {
  const defaultProps = {
    title: 'Test Module',
    subtitle: 'Test module description',
    icon: 'üçΩÔ∏è'
  };

  test('‚úÖ ModuleHeader doƒüru render ediliyor', () => {
    render(<ModuleHeader {...defaultProps} />);
    
    // Title'ƒ± arayalƒ±m
    const titleElement = screen.getByText('Test Module');
    expect(titleElement).toBeInTheDocument();
    
    // Subtitle'ƒ± kontrol edelim
    const subtitleElement = screen.getByText('Test module description');
    expect(subtitleElement).toBeInTheDocument();
  });

  test('‚úÖ ModuleHeader children ile √ßalƒ±≈üƒ±yor', () => {
    render(
      <ModuleHeader {...defaultProps}>
        <button>Test Button</button>
      </ModuleHeader>
    );
    
    expect(screen.getByRole('button', { name: 'Test Button' })).toBeInTheDocument();
  });

  test('‚úÖ ModuleHeader subtitle olmadan √ßalƒ±≈üƒ±yor', () => {
    const { subtitle, ...propsWithoutSubtitle } = defaultProps;
    
    render(<ModuleHeader {...propsWithoutSubtitle} />);
    
    const titleElement = screen.getByText('Test Module');
    expect(titleElement).toBeInTheDocument();
    expect(screen.queryByText('Test module description')).not.toBeInTheDocument();
  });

  test('‚úÖ ModuleHeader icon olmadan √ßalƒ±≈üƒ±yor', () => {
    const { icon, ...propsWithoutIcon } = defaultProps;
    
    render(<ModuleHeader {...propsWithoutIcon} />);
    
    const titleElement = screen.getByText('Test Module');
    expect(titleElement).toBeInTheDocument();
    expect(screen.queryByText('üçΩÔ∏è')).not.toBeInTheDocument();
  });
}); 